import styles from "../components/LoginForm/LoginForm.module.css";

export const activeInputStyles = (input, label, state) => {

  if (state) {

    label.classList.add(styles.activeLabel);
    input.classList.add(styles.activeInputField);
  } else {

    label.classList.remove(styles.activeLabel);
    input.classList.remove(styles.activeInputField);
  }
};
