import * as XLSX from "xlsx/xlsx.mjs";
import { getDate } from "./getDate";
import { getXlsxHeaders } from "./getXlsxHeaders";

export const exportToXlsx = (sheetData, setResources) => {

  let wb = XLSX.utils.book_new();
  let myHeader = getXlsxHeaders(sheetData);
  let ws = XLSX.utils.json_to_sheet(sheetData, {header: myHeader});
  XLSX.utils.book_append_sheet(wb, ws, "Sheet1");
  XLSX.writeFile(wb, `Resourses-${getDate()}.xlsx`);
  setResources([]);
};
