import { getNextPageUrl } from "../utils/getNextPageUrl";
import { getWorkSkills } from "../utils/getWorkSkills";

export const fetchResources = (
  setResources,
  setTotalResults,
  setNextPageUrl,
  url,
  setVisibleSpinner,
  skillsNames,
  authCode,
  skillLevelField,
  skillNameField,
  skillLabelField,
  recourceNameField
) => {
  // let username = "oracle_coe@cbt";
  // let password = "039b5654dd04d49e58bea640854e6dfdc650d1db9cc6b376ba5b6e2a199d";
  setVisibleSpinner(true);

  let initObj = {
    method: "GET",
    headers: {
      Authorization: "Basic " + authCode,
      "Content-Type": "application/json; charset=UTF-8",
      "Access-Control-Allow-Origin": "*",
    },
  };

  fetch(url, initObj)
    .then((response) => response.json())
    .then((json) => {
      return (
        getWorkSkills(json, setResources, skillsNames, skillLevelField, skillNameField, skillLabelField, recourceNameField),
        setTotalResults(json.totalResults),
        getNextPageUrl(json, setNextPageUrl)
      );
    })
    .catch((error) => console.debug("🚀 ~ file: fetchData.js ~ line 21 ~ error: " + error.message))
    .finally(() => setVisibleSpinner(false));
};
