export const getXlsxHeaders = (sheetData) => {
  let headers = [];


  for (let key in sheetData) {
    for (let header in sheetData[key]) {
      if (!headers.includes(`${header}`)) {
        headers = [...headers, `${header}`];
      }
    }
  }

  return headers.sort((a, b) => {
    if (Number(a[2]+a[3]+a[4]) > Number(b[2]+b[3]+b[4])) {
      return 1;
    }
    if (Number(a[2]+a[3]+a[4]) < Number(b[2]+b[3]+b[4])) {
      return -1;
    }
    return 0;
  });
};
