import { getNextPageUrl } from "../utils/getNextPageUrl";

export const fetchSkillsNames = async (url, setSkillsNames, authCode, setVerification, setTotalSkillNamesResults, setNextSkillNamesPageUrl) => {
  let response = false;

  let initObj = {
    method: "GET",
    headers: {
      Authorization: "Basic " + authCode,
      "Content-Type": "application/json; charset=UTF-8",
      "Access-Control-Allow-Origin": "*",
    },
  };

  await fetch(url, initObj)
    .then((response) => response.json())
    .then((json) => {
      if (json.items) {
        return (
          setSkillsNames((prevState) => [...prevState, ...json.items]),
          setTotalSkillNamesResults(json.totalResults),
          getNextPageUrl(json, setNextSkillNamesPageUrl),
          response = true
        );
      }
    })
    .catch((error) => console.debug("🚀 ~ file: getData.js ~ line 21 ~ error: " + error.message))
    .finally(() => response && setVerification(true));
};
