import React from "react";
import { RESOURCE_NAME, SKILL_LABEL_FIELD, SKILL_LEVEL_FIELD, WORK_SKILLS_NAME } from "../../../utils/constList";

import styles from "./ExportOptions.module.css";

export default function ExportOptions({
  skillLevelField,
  setSkillLevelField,
  skillNameField,
  setSkillNameField,
  skillLabelField,
  setSkillLabelField,
  recourceNameField,
  setRecourceNameField,
}) {
  const handleChangeSkillLevel = (event) => {
    setSkillLevelField(event.target.checked);
  };

  const handleChangeSkillName = (event) => {
    setSkillNameField(event.target.checked);
  };

  const handleChangeSkillLabel = (event) => {
    setSkillLabelField(event.target.checked);
  };

  const handleChangeResourceName = (event) => {
    setRecourceNameField(event.target.checked);
  };

  return (
    <div className={styles.wrapExportOptions}>
      <div className={styles.wrapExportOptionsTitle}>
        <span>Export options</span>
      </div>

      <label className={styles.exportOptions} id={SKILL_LEVEL_FIELD}>
        <input
          type="checkbox"
          id={SKILL_LEVEL_FIELD}
          name={SKILL_LEVEL_FIELD}
          value={skillLevelField}
          checked={skillLevelField}
          onChange={handleChangeSkillLevel}
        />
        Skill level
      </label>

      <label className={styles.exportOptions} id={WORK_SKILLS_NAME}>
        <input 
          type="checkbox" 
          id={WORK_SKILLS_NAME} 
          name={WORK_SKILLS_NAME} 
          value={skillNameField} 
          checked={skillNameField} 
          onChange={handleChangeSkillName} 
        />
        Skill name
      </label>

      <label className={styles.exportOptions} id={SKILL_LABEL_FIELD}>
        <input
          type="checkbox"
          id={SKILL_LABEL_FIELD}
          name={SKILL_LABEL_FIELD}
          value={skillLabelField}
          checked={skillLabelField}
          onChange={handleChangeSkillLabel}
        />
        Skill label
      </label>

      <label className={styles.exportOptions} id={RESOURCE_NAME}>
        <input
          type="checkbox"
          id={RESOURCE_NAME}
          name={RESOURCE_NAME}
          value={recourceNameField}
          checked={recourceNameField}
          onChange={handleChangeResourceName}
        />
        Resource name
      </label>
      
    </div>
  );
}
