import React, { useEffect, useRef } from "react";
import { activeInputStyles } from "../../utils/activeInputStyles";
import { fetchSkillsNames } from "../../API/fetchSkillsNames";
import { SKILLS_NAME_URL } from "../../utils/constList";

import styles from "./LoginForm.module.css";
import appStyles from "../../App.module.css";

export default function LoginForm({
  username,
  setUsername,
  password,
  setSkillsNames,
  setPassword,
  authCode,
  setVerification,
  setTotalSkillNamesResults,
  setNextSkillNamesPageUrl
}) {
  const userNameInput = useRef(null);
  const userNameLabel = useRef(null);
  const passwordInput = useRef(null);
  const passwordLabel = useRef(null);

  useEffect(() => {

    if(userNameInput) {

      activeInputStyles(userNameInput.current, userNameLabel.current, username);
    }
    
    if (passwordInput) {
      
      activeInputStyles(passwordInput.current, passwordLabel.current, password);
    }
  }, [username, password]);

  return (
    <form
      className={styles.wrapRequestForm}
      onSubmit={(event) => {
        return ( fetchSkillsNames(SKILLS_NAME_URL, setSkillsNames, authCode, setVerification, setTotalSkillNamesResults, setNextSkillNamesPageUrl), event.preventDefault());
      }}
    >
      <input
        className={styles.requestFormField}
        type="text"
        value={username}
        autoComplete="off"
        onChange={(event) => {
          setUsername(event.target.value);
        }}
        required
        ref={userNameInput}
      />
      <label ref={userNameLabel} className={styles.requestFormLabel}>
        User name
      </label>

      <input
        className={styles.requestFormField}
        type="password"
        value={password}
        autoComplete="off"
        onInput={(event) => {
          setPassword(event.target.value);
        }}
        required
        ref={passwordInput}
      />
      <label ref={passwordLabel} className={styles.requestFormLabel}>
        Password
      </label>

      <button className={appStyles.btn} type="submit">
        Login
      </button>
    </form>
  );
}
