import { RESOURCE_ID } from "./constList";

export const sortResourceObject = (obj, setResources) => {
  obj.sort(function (a, b) {
    if (a[RESOURCE_ID] > b[[RESOURCE_ID]]) {
      return 1;
    }
    if (a[[RESOURCE_ID]] < b[[RESOURCE_ID]]) {
      return -1;
    }
    return 0;
  });
  setResources(obj);
};
