import React, { useState, useEffect } from "react";
import { fetchResources } from "./API/fetchResources";
import { fetchSkillsNames } from "./API/fetchSkillsNames";
import LoginForm from "./components/LoginForm/LoginForm";
import RequestSelector from "./components/RequestSelector/RequestSelector";
import Spinner from "./components/Spinner/Spinner";
import { BASE_URL, EXPAND_WORKSKILLS } from "./utils/constList";
import { exportToXlsx } from "./utils/exportToXlsx";

import styles from "./App.module.css";
import { sortResourceObject } from "./utils/sortResourceObject";

export default function App() {
  const [resources, setResources] = useState([]);
  const [skillsNames, setSkillsNames] = useState([]);
  const [totalResults, setTotalResults] = useState(null);
  const [nextPageUrl, setNextPageUrl] = useState(null);
  const [totalSkillNamesResults, setTotalSkillNamesResults] = useState(null);
  const [nextSkillNamesPageUrl, setNextSkillNamesPageUrl] = useState(null);
  const [visibleSpinner, setVisibleSpinner] = useState(false);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [authCode, setAuthCode] = useState("");
  const [loadComplete, setLoadComplete] = useState(false);
  const [nameListLoadComplete, setNameListLoadComplete] = useState(false);
  const [verification, setVerification] = useState(false);
  const [skillLevelField, setSkillLevelField] = useState(false);
  const [skillNameField, setSkillNameField] = useState(true);
  const [skillLabelField, setSkillLabelField] = useState(false);
  const [recourceNameField, setRecourceNameField] = useState(false);

  useEffect(() => {
    if ((username, password)) {
      setAuthCode(window.btoa(username + ":" + password));
    }
  }, [username, password]);

  useEffect(() => {
    if (nextSkillNamesPageUrl && skillsNames.length < totalSkillNamesResults) {
      fetchSkillsNames(nextSkillNamesPageUrl, setSkillsNames, authCode, setVerification, setTotalSkillNamesResults, setNextSkillNamesPageUrl);
    }
  }, [totalSkillNamesResults, nextSkillNamesPageUrl, authCode, skillsNames.length]);

  useEffect(() => {
    if (!loadComplete && skillsNames && totalResults && resources.length < totalResults) {
      fetchResources(
        setResources,
        setTotalResults,
        setNextPageUrl,
        nextPageUrl + EXPAND_WORKSKILLS,
        setVisibleSpinner,
        skillsNames,
        authCode,
        skillLevelField,
        skillNameField,
        skillLabelField,
        recourceNameField
      );
    }
  }, [totalResults, nextPageUrl, authCode, recourceNameField, skillLabelField, skillLevelField, skillNameField, skillsNames]);

  useEffect(() => {
    if (resources.length === totalResults) {
      setLoadComplete(true);
    }

    if (loadComplete) {
      sortResourceObject(resources, setResources);
      setUsername("");
      setPassword("");
      console.debug("🚀 ~ file: App.js ~ line 71 ~ App ~ resources", resources);
    }
  }, [resources.length, totalResults, loadComplete, resources, skillsNames]);
  
  useEffect(()=>{

    if(skillsNames.length === totalSkillNamesResults) {
      
      setNameListLoadComplete(true);
      console.debug("🚀 ~ file: App.js ~ line 80 ~ App ~ skillsNames", skillsNames);
    }
  },[skillsNames.length, skillsNames, totalSkillNamesResults]);

  return (
    <div className={styles.appWrapp}>
      {!verification && (
        <LoginForm
          setUsername={setUsername}
          username={username}
          setPassword={setPassword}
          password={password}
          setSkillsNames={setSkillsNames}
          authCode={authCode}
          setVerification={setVerification}
          setTotalSkillNamesResults={setTotalSkillNamesResults}
          setNextSkillNamesPageUrl={setNextSkillNamesPageUrl}
        />
      )}

      {verification && nameListLoadComplete && (
        <RequestSelector
          setResources={setResources}
          setTotalResults={setTotalResults}
          setNextPageUrl={setNextPageUrl}
          url={BASE_URL}
          setVisibleSpinner={setVisibleSpinner}
          skillsNames={skillsNames}
          authCode={authCode}
          skillLevelField={skillLevelField}
          setSkillLevelField={setSkillLevelField}
          skillNameField={skillNameField}
          setSkillNameField={setSkillNameField}
          skillLabelField={skillLabelField}
          setSkillLabelField={setSkillLabelField}
          recourceNameField={recourceNameField}
          setRecourceNameField={setRecourceNameField}
          setLoadComplete={setLoadComplete}
        />
      )}

      <div className={styles.downloadBtnWrap}>
        {loadComplete && !visibleSpinner && (
          <button className={styles.btn} onClick={() => exportToXlsx(resources, setResources)}>
            Download
          </button>
        )}
      </div>

      {visibleSpinner && <Spinner />}
    </div>
  );
}
