import React from "react";
import { fetchResources } from "../../API/fetchResources";
import ExportOptions from "./ExportOptions/ExportOptions";

import styles from "./RequestSelector.module.css";
import appStyles from "../../App.module.css";

export default function RequestSelector({
  setResources,
  setTotalResults,
  setNextPageUrl,
  url,
  setVisibleSpinner,
  skillsNames,
  authCode,
  skillLevelField,
  setSkillLevelField,
  skillNameField,
  setSkillNameField,
  skillLabelField,
  setSkillLabelField,
  recourceNameField,
  setRecourceNameField,
  setLoadComplete
}) {
  return (
    <div className={styles.wrapRequestSelector}>
      <ExportOptions
        skillLevelField={skillLevelField}
        setSkillLevelField={setSkillLevelField}
        skillNameField={skillNameField}
        setSkillNameField={setSkillNameField}
        skillLabelField={skillLabelField}
        setSkillLabelField={setSkillLabelField}
        recourceNameField={recourceNameField}
        setRecourceNameField={setRecourceNameField}
      />
      <button
        className={appStyles.btn}
        onClick={() => {
          return (
            setResources([]),
            setLoadComplete(false),
            fetchResources(
              setResources,
              setTotalResults,
              setNextPageUrl,
              url,
              setVisibleSpinner,
              skillsNames,
              authCode,
              skillLevelField,
              skillNameField,
              skillLabelField,
              recourceNameField
            )
          );
        }}
      >
        Export resources
      </button>
    </div>
  );
}
