import {
  PARENT_RESOURCE_ID,
  PARENT_RESOURCE_INTERNAL_ID,
  RESOURCE_ID,
  RESOURCE_INTERNAL_ID,
  RESOURCE_NAME,
  WORK_SKILL_FIELD,
  WORK_SKILLS,
  SKILL_LEVEL_FIELD,
  TOTAL_NUMBER_OF_SKILLS,
} from "./constList";
import { counter } from "./counter";
import { getSkillName } from "./getSkillName";

export const getWorkSkills = (json, setResources, skillsNames, skillLevelField, skillNameField, skillLabelField, recourceNameField) => {
  for (let item in json.items) {
    let obj = {};

    if (!json.items[item].workSkills.items) {
      if (recourceNameField) {
        obj[RESOURCE_NAME] = json.items[item][RESOURCE_NAME];
      }
      obj[RESOURCE_ID] = json.items[item][RESOURCE_ID];
      obj[RESOURCE_INTERNAL_ID] = json.items[item][RESOURCE_INTERNAL_ID];
      obj[PARENT_RESOURCE_ID] = json.items[item][PARENT_RESOURCE_ID];
      obj[PARENT_RESOURCE_INTERNAL_ID] = json.items[item][PARENT_RESOURCE_INTERNAL_ID];
      obj[TOTAL_NUMBER_OF_SKILLS] = 0;

      setResources((prevState) => [...prevState, obj]);
    } else {
      if (recourceNameField) {
        obj[RESOURCE_NAME] = json.items[item][RESOURCE_NAME];
      }
      obj[RESOURCE_ID] = json.items[item][RESOURCE_ID];
      obj[RESOURCE_INTERNAL_ID] = json.items[item][RESOURCE_INTERNAL_ID];
      obj[PARENT_RESOURCE_ID] = json.items[item][PARENT_RESOURCE_ID];
      obj[PARENT_RESOURCE_INTERNAL_ID] = json.items[item][PARENT_RESOURCE_INTERNAL_ID];
      obj[TOTAL_NUMBER_OF_SKILLS] = Object.keys(json.items[item].workSkills.items).length;

      let workSkillsList = json.items[item][WORK_SKILLS].items;

      for (let skill in workSkillsList) {

        for (let field in workSkillsList[skill]) {
          
          if (skillLevelField && (field === SKILL_LEVEL_FIELD)) {

            obj[`ws${counter(Number(skill)+1)}_${field}`] = json.items[item][WORK_SKILLS].items[skill][field];

          } else if (skillLabelField && (field === WORK_SKILL_FIELD)) {

            obj[`ws${counter(Number(skill)+1)}_${field}`] = json.items[item][WORK_SKILLS].items[skill][field];

          } else if (field !== WORK_SKILL_FIELD && field !== SKILL_LEVEL_FIELD) {
            
            obj[`ws${counter(Number(skill)+1)}_${field}`] = json.items[item][WORK_SKILLS].items[skill][field];
          }

          if (skillNameField && field === WORK_SKILL_FIELD) {
            let name = getSkillName(skillsNames, json.items[item][WORK_SKILLS].items[skill][field]);
            obj[`ws${counter(Number(skill)+1)}_name`] = name;
          }
        }
      }

      setResources((prevState) => [...prevState, obj]);
    }
  }
};
